* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.quote-container {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

#quote-box {
  width: 550px;
  padding: 50px;
  border-radius: 3px;
  background-color: #fff;
  position: relative;
}

.fa-quote-left {
  font-size: 20px;
}

.fa-quote-right {
  font-size: 20px;
  text-align: right;
  position: absolute;
  right: 50px;
}

#text {
  text-align: center;
  font-size: 1.6rem;
  margin: 0;
}

#author {
  text-align: right;
  color: rgb(80, 80, 80);
  font-weight: 500;
}

.buttons {
  display: flex;
  justify-content: space-between;
}

#quote-box .btn {
  color: white;
  font-weight: 600;
}
